<template>
<example-all :my-list="menu">

</example-all>
</template>

<script>
import{reactive,onMounted} from 'vue';
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleAll=defineAsyncComponent(()=>import('@/components/main/ExampleAll'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "AllGoals",
  components: {ExampleAll},
  setup(){
    const menu=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsChampion()
      .then((response)=>{
        if(response.data.success){
          menu.push(response.data.data.goal)
        }
      })
      .catch((error)=>{
        handleError(error)
      })

    })
    return{
      menu,
      result,
    }
  }
}
</script>

